body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left;
}

.App-link {
  color: #61dafb;
}

.page-card {
   border: 2px solid #eee;
   display: flex;
   min-height: 100vh; 
}

.page-desc {
   flex-grow: 4;
   padding-left: 20px;
}

.page-pic {
   flex-grow: 8;
   background: purple;
}

.page-nos-indicator {
   position: fixed;
   background: transparent;
   bottom: 180px;
   right: 10px;
}

.highlighter-circle {
   width: 15px;
   height: 15px;
   border-radius: 50%;
   border: 1px solid white; 
   margin: 8px;
   flex-grow: 5;
}
.fill {
   background: white;
}
.home-read-more {
   padding: 10px;
   border: none;
   border-radius: 3px;
   background: lightblue;
   font-size: 15px;
   cursor: pointer;
   margin: 20px;
}
.page-pos-box {
  display: flex; 
}
.page-no-txt {
  color: white;
  flex-grow: 5;
  padding: 7px;
}
.fill-box {
  background: #79a;
  border-radius: 5px;
  font-weight: bold;
}
a {
  text-decoration: none;
  }

